section.notes-container {
	flex-grow: 1;
	display: flex;
	flex-direction: column;
	.flex-container {
		width: 100%;
		display: flex;
		flex-grow: 1;
		overflow: hidden;

		padding-top: 20px;
		margin-top: -20px;
		& > :nth-child(1) {
			width: 100%;
			padding: 12px;
			padding-top: 12px;
			overflow: auto;
			list-style: none;
			.no-note {
				display: none;
				width: 100%;
			}

			&.empty {
				max-width: unset;
				.no-note {
					align-items: center;
					display: flex;
					justify-content: center;
					height: 100%;
					flex-direction: column;
					h6 {
						font-size: 35px;
						color: #323e7e;
						font-weight: 700;
						margin-bottom: 10px;
					}
					p {
						font-size: 16px;
						color: #8990bb;
						margin-bottom: 30px;
						font-weight: 100;
					}
					svg {
						opacity: 0.7;
						width: 70%;
						max-width: 600px;
						height: auto;
					}
					@media (max-width: 768px) {
						justify-content: flex-start;
						padding-top: 10%;
						svg {
							width: 80%;
						}
					}
				}
			}

			&.removed-notes {
				max-width: unset;
				flex-grow: 1;
				display: flex;
				flex-wrap: wrap;
				align-content: flex-start;
				display: flex;
				justify-content: flex-start;
				align-items: flex-start;
				li {
					margin: 6px;
					min-width: 350px;
					flex-grow: 1;
				}
			}
		}
	}
}

@media (min-width: 992px) {
	section.notes-container .flex-container {
		& > :nth-child(1) {
			width: 100%;
			scrollbar-width: thin;
			scrollbar-color: rgb(162, 170, 185) rgba(192, 200, 216, 0.658);
			&::-webkit-scrollbar {
				background-color: rgba(192, 200, 216, 0.658);
				width: 8px;
			}
			&::-webkit-scrollbar-thumb {
				background-color: rgb(162, 170, 185);
			}
			&.empty .no-note {
				h6 {
					font-size: 50px;
					font-weight: 700;
					margin-bottom: 20px;
				}
				p {
					font-size: 20px;
					color: #666d99;
					margin-bottom: 30px;
					font-weight: 100;
				}
				svg {
					opacity: 0.7;
					width: 70%;
					max-width: 600px;
					height: auto;
				}
			}

			&.removed-notes {
				ul.notes {
					max-width: unset;
					flex-grow: 1;
				}
			}
		}
	}
}

@media (min-width: 1400px) {
	section.notes-container .flex-container {
		ul.notes {
			max-width: 550px;
			&.empty .no-note {
				h6 {
					font-size: 80px;
				}
				svg {
					width: 70%;
					max-width: 800px;
					height: auto;
				}
			}
		}

		& > :nth-child(2) {
			min-width: 600px;
			position: static;
			padding: 12px;
		}
	}
}
