.modal {
	position: fixed;
	top: 20px;
	width: 100%;
	max-width: 500px;
	z-index: 100;
	right: 0;
	left: 0;
	margin: auto;
	animation-name: show-modal;
	animation-duration: 0.5s;
	animation-fill-mode: forwards;
	.header {
		position: relative;
		z-index: 1;

		border-radius: 6px 6px 0 0;
		background-color: var(--gold);
		height: 40px;
		display: flex;
		align-items: center;
		justify-content: flex-end;
		padding-left: 6px;
		margin: 0 10px;
		button {
			padding: 7px;
			box-sizing: content-box;
			cursor: pointer;
			display: flex;
		}
	}

	.body {
		box-shadow: 0 0 30px #858c93;
		border-radius: 0 0 6px 6px;
		background-color: white;
		margin: 0 10px;
		padding: 24px;
		h3 {
			font-size: 18px;
			font-weight: 700;
			color: #1d3160;
			text-align: center;
			padding-bottom: 20px;
		}
		input {
			border: 1px solid #2333705d;
			padding: 10px;
			font-weight: 300;
			border-radius: 6px;
			color: #405077;
			font-size: 14px;
			height: 40px;
			&::placeholder {
				color: #405077;
				font-weight: 100;
				opacity: 0.7;
			}
			&.invalid {
				outline: 2px solid #ff000098;
				border: 1px solid red;
				&::placeholder {
					color: red;
				}
			}
		}

		button.submit {
			margin-top: 15px;
			background: var(--blue-primary);
			color: white;
			font-size: 16px;
			font-weight: 300;
			height: 40px;
			width: 100px;
			border-radius: 6px;
			transition: all 0.3s;
			&:hover {
				background-color: var(--blue-secondary);
			}
		}
	}
}

@keyframes show-modal {
	0% {
		opacity: 0;
		transform: scale(1.2);
	}
	100% {
		opacity: 1;
		transform: scale(1);
	}
}

@keyframes hide-modal {
	0% {
		opacity: 1;
		transform: scale(1);
	}
	100% {
		opacity: 0;
		transform: scale(1.2);
	}
}
