.App {
	width: 100vw;
	height: 100vh;
}
:root {
	--color-primary: white;
	--color-secondary: #f5f8ff;
	--blue-primary: #0087ff;
	--blue-secondary: #0068cf;
	--gold: #fbc900;
	--text-primary: #294b81;
	--shadow-color: red;
}

@font-face {
	font-family: 'Inter';
	src: url('../assets/fonts/Inter-Regular.woff2');
	font-weight: 100;
}
@font-face {
	font-family: 'Inter';
	src: url('../assets/fonts/Inter-Medium.woff2');
	font-weight: 300;
}
@font-face {
	font-family: 'Inter';
	src: url('../assets/fonts/Inter-SemiBold.woff2');
	font-weight: 500;
}

@font-face {
	font-family: 'Inter';
	src: url('../assets/fonts/Inter-Bold.woff2');
	font-weight: 700;
}

* {
	font-family: 'Inter';
}

body {
	background-color: var(--color-secondary);
}

body.night * {
	box-shadow: none !important;
}

.overlay {
	transition: all 0.5s;
	width: 100vw;
	height: 100vh;
	background-color: rgba(57, 90, 132, 0.53);
	position: fixed;
	z-index: 50;
	top: 0;
	-webkit-backdrop-filter: blur(2px);
	backdrop-filter: blur(2px);
	opacity: 0;
	visibility: hidden;
	cursor: pointer;
	&.visible {
		opacity: 1;
		visibility: visible;
	}
}

.fade-appear,
.fade-enter {
	opacity: 0;
	z-index: 1;
}
.fade-appear-active,
.fade-enter.fade-enter-active {
	opacity: 1;
	transition: opacity 450ms linear 250ms;
}
.fade-exit {
	opacity: 1;
}
.fade-exit.fade-exit-active {
	opacity: 0;
	transition: opacity 150ms linear;
}
