ul.toolbar {
	background-color: var(--blue-secondary);
	list-style: none;
	display: flex;
	align-items: center;
	border-radius: 6px 6px 0 0;
	height: 60px;
	padding: 4px 10px 0 10px;
	position: relative;
	li {
		button {
			position: relative;
			padding: 0 15px;
			font-size: 16px;
			font-weight: 300;
			width: 20px;
			height: 20px;
			svg {
				&:first-child {
					animation: none;
					position: static;
				}
				height: 100%;
				width: 100%;
				position: absolute;
				right: 0;
				left: 0;
				top: 0;
				bottom: 0;
				animation-name: fade-out-clone;
				animation-duration: 0.3s;
				animation-iteration-count: 1;
				animation-fill-mode: forwards;
				@keyframes fade-out-clone {
					to {
						transform: scale(2);
						opacity: 0;
					}
				}
			}
			ul.dropdown {
				transition: all 0.3s;
				background: white;
				width: 0;
				opacity: 0;
				height: 0;
				overflow: hidden;
				padding: 0;
				list-style-type: none;
				text-align: left;
				position: absolute;
				left: 0;
				box-shadow: 10px 9px 10px #487db726;
				top: -2px;
				color: #5c6999;
				overflow: hidden;
				border-radius: 6px 0 6px 0;
				z-index: 2;
				border-radius: 8px 16px 16px 16px;
				li {
					cursor: pointer;
					font-size: 15px;
					font-weight: 300;
					border-bottom: 1px solid #dce2f475;
					position: relative;
					text-align: center;
					height: 50px;
					display: flex;
					align-items: center;
					justify-content: center;
					label {
						display: flex;
						align-items: center;
						justify-content: center;
						cursor: pointer;
						text-align: center;
						position: absolute;
						left: 0;
						top: 0;
						height: 100%;
						width: 100%;
					}
					input {
						display: none;
					}
					&:last-of-type {
						border-bottom: 0;
					}

					&:hover {
						background-color: rgba(31, 49, 99, 0.048);
					}
				}
			}
		}
		&:first-of-type {
			button {
				position: static;
				&:focus ul.dropdown {
					opacity: 1;
					width: 150px;
					height: 150px;
				}
			}
		}
	}
	.tooltip {
		position: absolute;
		top: -10px;
		left: 0;
		visibility: hidden;
		opacity: 0;
		padding: 8px 20px;
		color: white;
		border-radius: 8px;
		font-size: 14px;
		font-weight: 300;
		top: -20px;
		background: var(--gold);
		opacity: 0;
		visibility: hidden;
		transition: all 0.3s;
		&::after {
			height: 10px;
			clip-path: polygon(50% 0, 100% 100%, 0 100%);
			content: ' ';
			display: block;
			background-color: #fbc900;
			position: absolute;
			transform: rotate(180deg);
			top: 29px;
			width: 15px;

			right: 0;
			left: 0;
			margin: auto;
		}
	}
	@media (max-width: 380px) {
		height: 50px;
		li button {
			padding: 0 13px;

			width: 15px;
			height: 15px;
		}
	}
}
