.note-view.placeholder {
	display: none;
	.box-shadow {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		align-items: stretch;
		h6 {
			font-size: 30px;
			font-weight: 500;
			color: #253266;
			text-align: center;
			margin-bottom: 10px;
		}
		p {
			font-size: 16px;
			font-weight: 100;
			text-align: center;
			color: rgb(96, 106, 145);
		}
		svg {
			width: 50%;
			height: max-content;
			margin: 0 auto;
			opacity: 0.7;
		}
	}
}

@media (min-width: 992px) {
	.note-view.placeholder {
		.box-shadow {
			h6 {
				font-size: 45px;
				font-weight: 700;
			}
			p {
				font-size: 20px;
				font-weight: 300;
			}
		}
	}
}

@media (min-width: 1400px) {
	.note-view.placeholder {
		display: block;
	}
}
