.alert {
	position: fixed;
	// top: 10px;
	top: -90px;
	right: 0px;
	left: 0;
	margin: auto;
	max-width: 350px;
	z-index: 150;
	background: white;
	width: 350px;
	border-radius: 6px;
	padding: 12px 20px 14px;
	box-shadow: 0 0 30px #555e771c;
	font-size: 16px;
	font-weight: 300;
	box-shadow: 0 0 30px #bbc5d2b3;
	color: #32406c;
	display: flex;
	align-items: center;
	justify-content: flex-start;
	overflow: hidden;
	animation: alert-animation;
	animation-iteration-count: 1;
	animation-fill-mode: forwards;
	height: 72px;
	// animation-timing-function: linear;
	box-sizing: border-box;
	.icon {
		margin-right: 20px;
		width: 35px;
		width: 35px;
		border-radius: 50%;
	}
	.timer {
		position: absolute;
		bottom: 0;
		width: 100%;
		left: 0;
		height: 4px;
		background: #cfd9e3;
		animation: decrese-width;
		animation-duration: 4s;
		animation-fill-mode: forwards;
		animation-timing-function: linear;
	}
	&.success {
		.success-icon {
			height: 32px;
			width: 32px;
		}
	}
}

@keyframes alert-animation {
	0% {
		top: -90px;
	}
	10% {
		top: 22px;
	}
	17% {
		top: 10px;
	}
	83% {
		top: 10px;
	}
	90% {
		top: 22px;
	}
	100% {
		top: -90px;
	}
}

@keyframes decrese-width {
	0% {
		width: 100%;
	}
	83% {
		width: 0;
	}
	100% {
		width: 0;
	}
}

@media (min-width: 768px) {
	.alert {
		top: -90px;
		// top: 25px;
		width: 400px;
		max-width: 500px;
		font-size: 18px;
		.icon {
			width: 38px;
			height: 38px;
		}
	}
}
