.add-note-container {
	padding: 12px;
	padding-top: 25px;
	height: 100%;
	display: flex;
	flex-direction: column;
	position: relative;
	z-index: 10;
	.textbox {
		padding: 20px;
		background-color: white;
		border-radius: 0 0 6px 6px;
		display: flex;
		box-shadow: 0 0 40px rgba(225, 232, 247, 0.534);
		flex-direction: column;
		& > div {
			display: flex;
			align-items: center;
			input {
				height: 40px;
				font-size: 20px;
				font-weight: 500;
				color: #314677;
				flex-grow: 1;
				&::placeholder {
					opacity: 1;
					color: #314677;
				}
			}
			button[type='submit'] {
				margin-top: 0;
				height: 40px;
				width: 130px;
				min-width: 130px;
				transition: all 0.3s;
				background-color: var(--gold);
				border-radius: 4px;
				font-size: 15px;
				font-weight: 300;
				color: white;
				transition: all 0.3s;
				&:hover {
					box-shadow: 0 0 40px #fbe20054;
				}
				&:active {
					box-shadow: none;
				}
			}
			margin-bottom: 10px;
		}
		textarea {
			display: block;
			width: 100%;
			border: none;
			box-shadow: 0 0 16px #7a9cd23b inset;
			border-radius: 5px;
			height: 100%;
			resize: none;
			padding: 12px;
			font-size: 14px;
			color: rgb(107, 123, 162);
			&:focus {
				outline: none;
			}
			&::placeholder {
				font-weight: 100;
				color: #31467798;
				opacity: 0.7;
			}
		}

		flex-grow: 1;
	}
	@media (min-width: 992px) {
		padding: 25px;
		.textbox {
			padding: 30px;
			textarea {
				font-size: 16px;
				padding: 18px;
			}
			& > div {
				input {
					font-size: 25px;
				}
				margin-bottom: 20px;
				button[type='submit'] {
					display: block;
					height: 50px;
					width: 170px;
					font-size: 17px;
				}
			}
		}
	}
}
