@import '../styles/loadingSkeleton.scss';

.note {
	background: white;
	border-radius: 6px;
	padding: 22px;
	margin-bottom: 14px;
	box-shadow: 0 0 40px #e8ebf5d7;
	border: 1px solid transparent;
	transition: all 0.3s;
	border: 1px solid #dde2f000;
	cursor: pointer;
	.header {
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin-bottom: 10px;
		margin-top: -5px;

		span.eye-icon {
			width: 19px;
			height: 20px;
			display: flex;
			svg {
				width: 100%;
				height: 100%;
				fill: #556682;
			}

			&:hover {
				fill: #263349;

				path {
					opacity: 1;
				}
			}
		}
		.note-controllers {
			display: flex;
			align-items: center;
			margin-right: -14px;
			margin-top: -10px;
			align-items: center;
			button {
				padding: 10px;
				svg {
					transition: all 0.3s;
					width: 17px;
					height: 17px;
					path {
						transition: all 0.3s;
					}
				}
				&:nth-child(1):hover svg {
					transform-origin: center;
					transform: rotate(-180deg);
				}
				&:nth-child(2):hover svg {
					path:nth-child(1) {
						opacity: 1;
					}
					path:nth-child(2) {
						opacity: 0.7;
					}
				}
			}
		}
		h6 {
			font-size: 18px;
			font-weight: 500;
			color: var(--blue-primary);
			&:empty {
				height: 20px;
				flex-grow: 1;
				margin-right: 40%;
				border-radius: 3px;
				opacity: 0.7;

				@include skeleton();
			}
		}
		&.empty {
			margin-top: 0;
		}
	}
	.content {
		line-height: 20px;
		font-size: 14px;
		font-weight: 100;
		color: #354771e8;
		margin-bottom: 10px;
		overflow: hidden;
		text-overflow: ellipsis;
		display: -webkit-box;
		-webkit-line-clamp: 3; /* number of lines to show */
		line-clamp: 3;
		-webkit-box-orient: vertical;
		&:empty {
			height: 50px;
			border-radius: 4px;
			@include skeleton();
			opacity: 0.7;
		}
	}
	.footer {
		display: flex;
		align-items: center;
		font-size: 13px;
		font-weight: 100;
		transform: translateY(4px);
		color: #8189a7;
		.updated {
			display: flex;
			align-items: center;
			&:empty {
				background-color: red;
				height: 10px;
				width: 40px;
				border-radius: 3px;
				@include skeleton();
				opacity: 0.7;
			}
			span {
				padding-right: 8px;
			}
		}
		.createdAt:empty {
			background-color: red;
			height: 10px;
			width: 40px;
			border-radius: 3px;
			opacity: 0.7;

			@include skeleton();
		}
	}
	&:hover {
		background-color: rgba(255, 255, 255, 0.547);
		box-shadow: 0 0 40px transparent;
		border-color: #dde2f083;
	}
}
.removed-note {
	cursor: default;
	&:hover {
		background: white;

		box-shadow: 0 0 40px #e8ebf5d7;

		border: 1px solid #dde2f000;
	}
}
