.note-view {
	width: 100%;
	position: fixed;
	top: 0;
	right: 0;
	width: 100%;
	height: 100%;
	z-index: 200;
	padding: 0;

	animation: show;
	animation-duration: 0.2s;
	animation-timing-function: linear;
	animation-fill-mode: forwards;

	.box-shadow {
		height: 100%;
		box-shadow: 0 0 40px rgba(232, 235, 245, 0.8431372549);
		border-radius: 10px;
		background-color: white;
		position: relative;
	}
	.header {
		color: white;
		display: flex;
		align-items: center;
		border-radius: 6px 6px 0 0;
		font-size: 14px;
		font-weight: 300;
		height: 60px;
		position: absolute;
		right: 0;
		top: 0;
		width: 100%;
		align-items: stretch;
		padding: 0;
		overflow-x: clip;
		overflow-y: visible;
		z-index: 10;
		button {
			display: flex;
			align-items: center;
			svg {
				width: 20px;
				height: 20px;
				margin-right: 12px;
			}
		}
		& > :nth-child(1) {
			display: flex;
			font-size: 16px;
			width: 50%;
			background: var(--blue-primary);
			padding: 0 16px;
			transition: all 0.3s;
		}

		& > :nth-child(2) {
			transition: all 0.3s;
			display: flex;
			button {
				margin-left: 14px;
				svg {
					width: 20px;
					height: 20px;
					margin-right: 0;
				}
			}
			padding: 0 16px;
			margin: 0;
			width: 50%;
			justify-content: flex-end;
			background: var(--blue-primary);
		}
		button.close {
			svg {
				background: #ffffff7a;
				border-radius: 4px;
				padding: 2px;
				box-sizing: content-box;
			}
		}
	}
	.body {
		& * {
			transition: all 0.3s;
		}
		overflow: hidden;
		position: relative;
		height: calc(100% - 60px);
		padding: 30px;
		display: flex;
		flex-direction: column;
		input {
			font-size: 24px;
			color: #263863;
			background-color: white;
			border: 1px solid transparent;
			transition: all 0.3s !important;
			margin-bottom: 10px;
		}
		& > :nth-child(2) {
			flex-grow: 1;
			background-color: white;
			transform: translate(0);
			z-index: 1;
			textarea {
				line-height: 24px;
				height: 100%;
				border: none;
				font-size: 15px;
				width: 100%;
				display: block;
				font-weight: 300;
				color: #263863ce;
				resize: none;
				border: 1px solid transparent;
				background-color: transparent;
				&:focus {
					outline: none;
				}
			}
		}
		.form-control-buttons {
			position: absolute;
			height: 40px;
			display: flex;
			align-items: stretch;
			bottom: -50px;
			button[type='submit'] {
				background-color: var(--blue-primary);
				border-radius: 4px;
				font-size: 15px;
				font-weight: 300;
				width: 120px;
				color: white;
				&:hover {
					box-shadow: 0 0 50px #0088ff38;
				}
				&:active {
					box-shadow: 0 0 0 transparent;
				}
			}
			button[type='reset'] {
				color: rgb(89, 103, 139);
				font-weight: 100;
				width: 100px;
				// color: rgb(31, 48, 94);
				&:hover {
					color: rgb(31, 48, 94);
				}
			}
		}
	}
	.toolbar .tooltip {
		display: none;
	}
	&.hide {
		animation-name: hide;
	}

	@keyframes show {
		from {
			opacity: 0;
			transform: scale(0.9) translate(0, 10px);
		}

		to {
			opacity: 1;
			transform: scale(1);
		}
	}
	@keyframes hide {
		from {
			opacity: 1;
			transform: scale(1);
		}

		to {
			opacity: 0;
			transform: scale(0.9) translate(0, 10px);
		}
	}
}
.note-view.editable {
	.header {
		transition-delay: 0.27s;
		z-index: -10;
		& > div:nth-child(1) {
			transform: translateX(-100%);
		}
		& > div:nth-child(2) {
			transform: translateX(100%);
		}
	}
	.body {
		input {
			border-color: #c6cfe4;
			border-radius: 5px;
			padding: 12px 16px;
		}
		& > :nth-child(2) {
			margin-bottom: 55px;
			textarea {
				border-color: #c6cfe4;
				border-radius: 5px;
				padding: 12px 16px;
				scrollbar-width: thin;
			}
		}
		.form-control-buttons {
			bottom: 30px;
		}
	}
}

@media (min-width: 1400px) {
	.note-view {
		animation-name: none;
		position: static;
		z-index: unset;
		.header {
			button.close {
				display: none;
			}
		}
		.toolbar .tooltip {
			display: block;
		}
	}
}
