@import '../styles/loadingSkeleton.scss';

aside {
	background-color: var(--color-primary);
	position: fixed;
	top: 0;
	left: -320px;
	z-index: 100;
	height: 100vh;
	width: 320px;
	min-width: 320px;
	padding: 20px;
	transition: all 0.5s ease-out;
	overflow: hidden;
	& > * {
		transition: all 0.3s;
	}
	button.close {
		margin-left: auto;
		margin-bottom: 20px;
		display: block;
		width: max-content;
		transition-delay: 0.17s;
		svg {
			width: 30px;
			height: 30px;
		}
	}
	label.user-photo {
		display: block;
		margin: auto;
		width: 131px;
		height: 131px;
		border-radius: 50%;
		margin-bottom: 20px;
		position: relative;
		.user-image-placeholder {
			width: 100%;
			height: 100%;
		}
		img {
			border-radius: 50%;
			object-fit: cover;
			@extend .user-image-placeholder;
		}
		button.edit {
			transition: all 0.3s;
			transition-delay: 0.17s;

			z-index: 100;
			position: absolute;
			right: -13px;
			bottom: -20px;
			svg {
				width: 20px;
				height: 20px;
			}
		}
		&:empty {
			@include skeleton();
		}
	}
	.upload-btn {
		transition: all 0.3s;
		transition-delay: 0.17s;
		background: var(--gold);
		border-radius: 50%;
		padding: 9px;
		height: 22px;
		width: 22px;
		display: flex;
		box-sizing: content-box;
		margin: -19px auto 0;
		box-shadow: 0 0 30px #d7deec;
		background: var(--color-primary);
		position: relative;
		transform: scale(0);
		svg {
			fill: var(--gold);
			stroke: var(--gold);
		}
	}
	.inputs-container {
		background-color: var(--color-primary);
		position: relative;
		z-index: 1;
		transition: all 0.3s;
		transition-delay: 0.17s;
		padding-bottom: 10px;
		input.user-name {
			display: block;
			margin: auto;
			font-size: 20px;
			text-align: center;
			font-weight: 700;
			color: #253266;
			background-color: transparent;
			border-radius: 8px;
			padding: 0 20px;
			box-sizing: border-box;
			border: 1px solid transparent;
			transition: all 0.3s;
			background-color: var(--color-primary);

			transition-delay: 0.17s;
			&[value=''] {
				display: none;
				& + div {
					height: 30px;
					width: 120px;
					margin: auto;
					border-radius: 6px;

					@include skeleton();
				}
			}

			&.invalid {
				transition-delay: 0 !important;
				margin-bottom: 10px;

				border-color: red !important;
			}
		}
		input.user-email {
			border: 1px solid transparent;
			transition: all 0.3s;
			width: 100%;
			padding: 0 40px;
			display: block;
			margin: auto;
			font-size: 14px;
			background-color: transparent;
			border-radius: 8px;
			font-weight: 300;
			background-color: var(--color-primary);
			color: #25326693;
			text-align: center;
			transition-delay: 0.17s;

			&[value=''] {
				display: none;
				& + div {
					height: 25px;
					width: 200px;
					margin: auto;
					border-radius: 4px;
					margin-top: 8px;
					@include skeleton();
				}
			}

			&.invalid {
				border-color: red !important;
				transition-delay: 0 !important;
				margin-bottom: 10px;
			}
		}

		.validation-error {
			width: 80%;
			z-index: -1;
			margin: auto;
			font-size: 13px;
			padding: 3px 3px;
			color: red;
			font-weight: 100;
			height: 10px;
			position: relative;
			top: -35px;
			transition: all 0.3s;
			&.visible {
				padding-bottom: 18px;
				top: -8px;
			}
		}
	}
	input[type='file'] {
		display: none;
	}
	.form-control-buttons {
		height: 42px;
		width: 80%;
		transition: all 0.3s;
		transition-delay: 0.17s;
		margin: -50px auto 0;
		display: flex;
		button[type='submit'] {
			transition: all 0.3s;
			width: 58%;
			background: var(--blue-secondary);
			height: 100%;
			border-radius: 6px;
			margin-right: auto;
			color: var(--color-primary);
			box-sizing: border-box;
			padding: 10px;
			font-size: 13px;
			font-weight: 300;
			display: block;
			&:hover {
				background: var(--blue-primary);
			}
		}
		button[type='reset'] {
			width: 40%;
			height: 100%;
			border-radius: 6px;
			color: rgb(29, 40, 90);
			box-sizing: border-box;
			padding: 10px;
			font-size: 13px;
			font-weight: 300;

			&:hover {
				color: black;
			}
		}
	}
	.logout {
		width: 80%;
		height: 48px;
		display: flex;
		align-items: center;
		justify-content: center;

		border-radius: 8px;
		margin: 17px auto 0;
		font-size: 16px;
		font-weight: 500;
		transition: all 0.3s;
		box-sizing: border-box;

		background-color: transparent;
		border: 1px solid var(--gold);
		color: var(--gold);
		transition-delay: 0.17s;
		svg {
			height: 22px;
			width: 22px;
			margin-left: 11px;
			transition: all 0.3s;
			fill: var(--gold) !important;
		}
		&:hover {
			background: var(--gold);
			color: var(--color-primary);
			svg {
				fill: var(--color-primary) !important;
			}
		}
	}
	ul {
		margin-top: 40px;
		li {
			transition: all 0.2s;
			list-style-type: none;
			a {
				color: #2c4173;
				font-size: 18px;
				font-weight: 300;
				display: flex;
				align-items: center;
				height: 55px;
				position: relative;
				transition: all 0.3s;
				border-radius: 12px;
				padding-left: 70px;
				span {
					margin-left: auto;
					margin-right: 20px;
					font-size: 16px;
					color: #5a71a6;
					&:empty {
						width: 10px;
						height: 10px;
						border-radius: 50%;
						background-color: #d7e0f1;
						@include skeleton();
					}
				}
				svg {
					position: absolute;
					left: 20px;
					top: 0;
					bottom: 0;
					transform: scale(1.1);
					margin: auto;
				}
				&.active {
					background: #0087ff17;
					color: #0087ff;
					svg {
						fill: var(--blue-primary);
						transition: all 0.3s;

						&.notes path:nth-of-type(1) {
							transition: all 0.3s;

							stroke: var(--blue-primary);
						}
					}
				}
			}
		}
		@each $child, $delay in (1: 0.15, 2: 0.1, 3: 0.05, 4: 0) {
			& > :nth-child(#{$child}) {
				transition-delay: #{$delay}s;
			}
		}
	}
	&.visible {
		left: 0;
	}
}

aside.editable {
	button.close {
		transform: translate(50px, -50px);
	}
	.user-photo {
		button.edit {
			transform: scale(0);
		}
	}
	.inputs-container {
		margin-top: 50px;
		input.user-name,
		input.user-email {
			padding: 0 3px;
			width: 80%;
			border: 1px solid #cdd2dd;
			color: #253266;
			font-size: 18px;
			font-weight: 500;
			padding: 12px 0;
		}
		input.user-email {
			padding: 15.5px 0;
			font-weight: 300;
			font-size: 13px !important;
		}
	}
	.upload-btn {
		transform: scale(1);
	}
	.form-control-buttons {
		margin-top: 10px;
	}
	.logout {
		transform-origin: bottom;
		transform: scaleY(0);
	}
	ul {
		li {
			transform: translateX(-300px);
		}
	}
}

@media (min-width: 992px) {
	aside {
		top: initial;
		left: initial;
		right: initial;
		left: initial;
		position: relative;
		box-shadow: 3px 0 10px #e1e8f7ad;
		padding-top: 70px;
		height: unset;
		z-index: unset;

		input.user-name {
			font-size: 20px;
		}
		input.user-email {
			font-size: 14px;
			color: #253266b4;
		}

		button.close {
			display: none;
		}
		ul li a {
			padding-left: 60px;
			font-size: 16px;
			svg {
				transform: scale(1);
			}
			&.active {
				// background: #f5f8ff;
				// box-shadow: 0 0 5px inset #c8d1e9c4;

				// width : 400px;
				&::before {
					background-color: red;
					content: ' ';
					// wi
				}
			}
		}
	}
}
