.route-visible {
	display: block;
}
.route-hidden {
	display: none;
}

* {
	padding: 0;
	margin: 0;
	font-size: 15px;
	color: black;
	vertical-align: baseline;
	box-sizing: border-box;
}

button,
i {
	box-sizing: content-box;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
	display: block;
}
html {
	scroll-behavior: smooth;
}
button {
	border: 1px solid rgb(54, 54, 54);
	background-color: transparent;
	cursor: pointer;
}
input {
	display: block;
	width: 100%;
}

span,
i {
	display: inline-block;
}
a {
	text-decoration: none;
	@extend span;
	&:focus {
		color: #0070ff;
	}
	cursor: pointer;
}
h1 {
	font-size: 30px;
}
h2 {
	font-size: 27px;
}
h3 {
	font-size: 23px;
}
h4 {
	font-size: 20px;
}
h5 {
	font-size: 18px;
}
h6 {
	font-size: 16px;
}
input {
	&:focus {
		outline: none;
	}
}
button,
input {
	border: none;
}
body {
	-webkit-text-size-adjust: 100%; // 3
	-webkit-tap-highlight-color: rgba(black, 0); // 4
}
sub,
sup {
	vertical-align: baseline;
}

sub {
	bottom: -0.25em;
}
sup {
	top: -0.5em;
}
::-moz-focus-inner {
	padding: 0;
	border-style: none;
}

textarea {
	resize: vertical; // 1
}

i,
span,
p,
li,
a,
button,
ul,
li,
ol,
td,
tr,
table,
tbody,
thead,
tfoot,
th,
form,
label,
input,
textarea,
div,
header,
nav,
main,
aside,
section {
	color: inherit;
	font-size: inherit;
	font-weight: inherit;
}
ul,
li {
	list-style-position: inside;
}
