.search-box {
	width: calc(100% - 20px);
	background: white;
	margin: 10px;
	border-radius: 8px;
	box-shadow: 0 0 40px rgba(232, 235, 245, 0.8431372549);
	padding: 14px;
	position: relative;
	input {
		border: 1px solid rgba(107, 122, 162, 0.397);
		border-radius: 6px;
		padding: 15px;
		font-weight: 300;
		font-size: 16px;
		color: rgb(56, 71, 107);
		padding-left: 52px;
		&::placeholder {
			color: rgb(56, 71, 107);
			font-weight: 100;
			opacity: 0.7;
		}
	}
	button {
		width: 40px;
		height: 40px;
		border-radius: 4px;
		box-sizing: border-box;
		padding: 7px;
		position: absolute;
		top: 20px;
		left: 20px;
		svg {
			width: 100%;
			height: 100%;
		}
	}
}
