header {
	nav {
		display: flex;
		padding: 0 30px;
		background-color: white;
		justify-content: space-between;
		height: 80px;
		align-items: center;
		box-shadow: 0 4px 5px #dce2f09c;
		z-index: 10;
		position: relative;
		.hamburger {
			display: flex;
			flex-direction: column;
			height: 20px;
			justify-content: space-between;
			.line-1 {
				border-radius: 11px;
				height: 4px;
				background: rgb(50, 57, 126);
				width: 27px;
				transition: all 0.5s;
			}
			.line-2 {
				@extend .line-1;
				transition: all 0.2s;
				transition: all 0.5s;
			}
			.line-3 {
				@extend .line-1;
				transition: all 0.5s;
			}
		}
	}
	@media (min-width: 992px) {
		display: none;
	}
}
