.form-container {
	position: absolute;
	right: 0;
	left: 0;
	top: 0;
	bottom: 0;
	margin: auto;
	width: 100vw;
	height: 100vh;
	display: flex;
	justify-content: center;
	padding: 20px;
	flex-direction: column;
	padding: 15px 20px 0;
	form {
		position: relative;
		overflow: hidden;
		background-color: var(--light);
		height: max-content;
		border-radius: 16px;
		padding: 55px 26px 45px;
		box-sizing: border-box;
		box-shadow: 10px 10px 30px rgba(214, 231, 249, 0.6117647059), -10px -10px 8px white;
		width: 100%;
		max-width: 380px;
		.loading {
			height: 5px;
			background: var(--gold);
			width: 100%;
			position: absolute;
			top: 0;
			right: 0;
			animation-name: loading;
			animation-duration: 1s;
			animation-iteration-count: infinite;
			animation-timing-function: cubic-bezier(1, 0.62, 0.31, 0.95);
		}
		.image-preview {
			cursor: pointer;
			margin: auto;
			width: max-content;
			height: 121px;
			width: 121px;
			overflow: hidden;
			border-radius: 50%;
			img {
				width: 100%;
				height: 100%;
			}
			svg {
				circle {
					transform: translate(0, -1px);
				}
			}
		}
		.title {
			margin-bottom: 30px;
			font-size: 16px;
			text-align: center;
			color: var(--blue-primary);
			font-weight: 100;
		}
		.upload-btn {
			cursor: pointer;
			width: max-content;
			display: block;
			margin: auto;
			border-radius: 50%;
			margin-bottom: 20px;
			margin-top: 8px;
			padding: 12px;
			display: flex;
			box-shadow: 3px 3px 5px rgba(214, 231, 249, 0.6117647059), -5px -5px 5px white,
				3px 3px 5px rgba(214, 231, 249, 0) inset, -5px -5px 5px rgba(255, 255, 255, 0) inset;
			transition-duration: 0.2s;
			&:active {
				box-shadow: 5px -3px 5px rgba(214, 231, 249, 0),
					-5px -5px 5px rgba(255, 255, 255, 0), 3px 3px 5px rgb(214, 231, 249) inset,
					-5px -5px 5px rgb(255, 255, 255) inset;
			}
			&:hover {
				#Path_4346 {
					animation: {
						name: flash-up;
						duration: 0.3s;
						direction: backward;
					}
					animation-name: flash-up;
					animation-duration: 0.3s;
				}
			}
		}
		.input-group {
			position: relative;
			border-radius: 8px;
			box-shadow: 3px 3px 5px rgba(214, 231, 249, 0.5607843137) inset,
				-5px -5px 6px white inset;
			transition: all 0.3s;
			input {
				padding: 10px;
				padding-left: 10px;
				font-size: 14px;
				padding-left: 50px;
				height: 50px;
				margin-top: 10px;
				background: var(--login-bg);
				color: var(--text-primary);
				font-weight: 100;
				&::placeholder {
					opacity: 0.7;
					color: var(--text-primary);
				}
			}
			&.invalid {
				background-color: #fff5f5;
				box-shadow: 3px 3px 5px rgba(249, 214, 214, 0.4) inset, -5px -5px 6px white inset;
			}

			svg {
				position: absolute;
				left: 18px;
				top: 0;
				bottom: 0;
				margin: auto;
			}
		}
		.validation-error {
			color: red;
			font-weight: 100;
			font-size: 14px;
			margin: 4px 0 0 4px;
			opacity: 0;
			animation-duration: 0.3s;
			animation-timing-function: linear;
			animation-fill-mode: forwards;
			animation-name: dd;
			height: 0;

			@keyframes dd {
				50% {
					height: 5px;
				}
				100% {
					height: 18px;
					opacity: 1;
				}
			}
		}
		.link {
			color: var(--text-primary);
			transform: translateY(20px);
			opacity: 0.6;
			font-weight: 100;
			margin: auto;
			display: block;
			text-align: center;
			font-weight: 300;
			u {
				color: var(--gold);
			}
			&:hover {
				opacity: 1;
			}
		}
		button[type='submit'] {
			padding: 13px;
			font-size: 16px;
			border-radius: 6px;
			background-color: var(--blue-secondary);
			color: white;
			width: 150px;
			font-weight: 30;
			margin-top: 25px;
			margin-bottom: 5px;
			margin-left: auto;
			display: block;
			transition: all .3s;
			&:hover {
				background-color: var(--blue-primary);
				box-shadow: 0 0px 50px #0068cf2a;
			}
			&:active {
				box-shadow: none;
			}
		}
		input[type='file'] {
			display: none;
			cursor: pointer;
		}
		.response-error {
			font-size: 16px;
			color: red;
			margin-top: 5px;
			font-weight: 300;
		}
	}
	form.login {
		.image-preview {
			cursor: default;
			margin-bottom: 20px;
		}
	}
	@media (min-width: 400px) {
		form {
			padding: 55px 45px 45px;
		}
	}
	@media (min-width: 992px) {
		align-items: center;
	}
}

@keyframes loading {
	from {
		right: -100%;
	}
	50% {
		transform: scaleX(0.9);
	}
	to {
		right: 100%;
	}
}

@keyframes flash-up {
	from {
		transform: translateY(0px);
	}
	to {
		transform: translateY(-5px);
	}
}
